@import '../../themes/default/elements/button.variables';

@mixin addConsequence($consequence) {
  $_backgroundColor: get('#{$consequence}.color', $consequence-colors-map);
  $_backgroundColorHover: get('#{$consequence}.hover', $consequence-colors-map);
  $_backgroundColorFocus: get('#{$consequence}.focus', $consequence-colors-map);
  $_backgroundColorDown: get('#{$consequence}.down', $consequence-colors-map);
  $_backgroundColorActive: get('#{$consequence}.active', $consequence-colors-map);
  $_textColor: $button-positive-text-color;
  $_textShadow: $button-positive-text-shadow;

  /* Standard */
  .ui.#{$consequence}.buttons .button,
  .ui.#{$consequence}.button {
    background-color: $_backgroundColor;
    color: $_textColor;
    text-shadow: $_textShadow;
    background-image: $button-colored-background-image;
  }
  .ui.#{$consequence}.button {
    box-shadow: $button-colored-box-shadow;
  }
  .ui.#{$consequence}.buttons .button:hover,
  .ui.#{$consequence}.button:hover {
    background-color: $_backgroundColorHover;
    color: $_textColor;
    text-shadow: $_textShadow;
  }
  .ui.#{$consequence}.buttons .button:focus,
  .ui.#{$consequence}.button:focus {
    background-color: $_backgroundColorFocus;
    color: $_textColor;
    text-shadow: $_textShadow;
  }
  .ui.#{$consequence}.buttons .button:active,
  .ui.#{$consequence}.button:active {
    background-color: $_backgroundColorDown;
    color: $_textColor;
    text-shadow: $_textShadow;
  }
  .ui.#{$consequence}.buttons .active.button,
  .ui.#{$consequence}.buttons .active.button:active,
  .ui.#{$consequence}.active.button,
  .ui.#{$consequence}.button .active.button:active {
    background-color: $_backgroundColorActive;
    color: $_textColor;
    text-shadow: $_textShadow;
  }

  @if($use-button-type-basic) {
    /* Basic */
    .ui.basic.#{$consequence}.buttons .button,
    .ui.basic.#{$consequence}.button {
      background: transparent;
      box-shadow: 0 0 0 $button-basic-border-size $_backgroundColor inset;
      color: $_backgroundColor;
    }
    .ui.basic.#{$consequence}.buttons .button:hover,
    .ui.basic.#{$consequence}.button:hover {
      background: transparent;
      box-shadow: 0 0 0 $button-basic-colored-border-size $_backgroundColorHover
        inset;
      color: $_backgroundColorHover;
    }
    .ui.basic.#{$consequence}.buttons .button:focus,
    .ui.basic.#{$consequence}.button:focus {
      background: transparent;
      box-shadow: 0 0 0 $button-basic-colored-border-size $_backgroundColorFocus
        inset;
      color: $_backgroundColorHover;
    }
    .ui.basic.#{$consequence}.buttons .active.button,
    .ui.basic.#{$consequence}.active.button {
      background: transparent;
      box-shadow: 0 0 0 $button-basic-colored-border-size $_backgroundColorActive
        inset;
      color: $_backgroundColorDown;
    }
    .ui.basic.#{$consequence}.buttons .button:active,
    .ui.basic.#{$consequence}.button:active {
      box-shadow: 0 0 0 $button-basic-colored-border-size $_backgroundColorDown
        inset;
      color: $_backgroundColorDown;
    }
    .ui.buttons:not(.vertical) > .basic.#{$consequence}.button:not(:first-child) {
      margin-left: -$button-basic-colored-border-size;
    }
  }
}

@if $use-button {
  /*******************************
  //@-        Button
  *******************************/
  
  .ui.button {
    cursor: pointer;
    display: inline-block;
  
    min-height: 1em;
  
    outline: none;
    border: none;
    vertical-align: $button-vertical-align;
    background: $button-background;
    color: $button-text-color;
  
    font-family: $button-font-family;
  
    margin: 0 $button-horizontal-margin $button-vertical-margin 0;
    padding: $button-vertical-padding $button-horizontal-padding ($button-vertical-padding + $button-shadow-offset);
  
    text-transform: $button-text-transform;
    text-shadow: $button-text-shadow;
    font-weight: $button-font-weight;
    line-height: $button-line-height;
    font-style: normal;
    text-align: center;
    text-decoration: none;
  
    border-radius: $button-border-radius;
    box-shadow: $button-box-shadow;
  
    user-select: none;
    transition: $button-transition;
    will-change: $button-will-change;
  
    -webkit-tap-highlight-color: $button-tap-color;
  }
  
  
  /*******************************
              States
  *******************************/
  
  /*--------------
        Hover
  ---------------*/
  
  .ui.button:hover {
    background-color: $button-hover-background-color;
    background-image: $button-hover-background-image;
    box-shadow: $button-hover-box-shadow;
    color: $button-hover-color;
  }
  
  .ui.button:hover .icon {
    opacity: $button-icon-hover-opacity;
  }
  
  /*--------------
        Focus
  ---------------*/
  
  .ui.button:focus {
    background-color: $button-focus-background-color;
    color: $button-focus-color;
    background-image: $button-focus-background-image;
    box-shadow: $button-focus-box-shadow;
  }
  
  .ui.button:focus .icon {
    opacity: $button-icon-focus-opacity;
  }
  
  /*--------------
        Down
  ---------------*/
  
  .ui.button:active,
  .ui.active.button:active {
    background-color: $button-down-background-color;
    background-image: $button-down-background-image;
    color: $button-down-color;
    box-shadow: $button-down-box-shadow;
  }
  
  /*--------------
       Active
  ---------------*/
  
  .ui.active.button {
    background-color: $button-active-background-color;
    background-image: $button-active-background-image;
    box-shadow: $button-active-box-shadow;
    color: $button-active-color;
  }
  .ui.active.button:hover {
    background-color: $button-active-hover-background-color;
    background-image: $button-active-hover-background-image;
    color: $button-active-hover-color;
  }
  .ui.active.button:active {
    background-color: $button-active-background-color;
    background-image: $button-active-background-image;
  }
  
  
  /*--------------
      Loading
  ---------------*/
  
  /* Specificity hack */
  .ui.loading.loading.loading.loading.loading.loading.button {
    position: relative;
    cursor: default;
    text-shadow: none !important;
    color: transparent;
    opacity: $button-loading-opacity;
    pointer-events: $button-loading-pointer-events;
    transition: $button-loading-transition;
  }
  .ui.loading.button:before {
    position: absolute;
    content: '';
    top: 50%;
    left: 50%;
  
    margin: $loader-margin;
    width: $loader-size;
    height: $loader-size;
  
    border-radius: $circular-radius;
    border: $loader-line-width solid $button-inverted-loader-fill-color;
  }
  .ui.loading.button:after {
    position: absolute;
    content: '';
    top: 50%;
    left: 50%;
  
    margin: $loader-margin;
    width: $loader-size;
    height: $loader-size;
  
    border-radius: $circular-radius;
  
    animation: loader $loader-speed infinite linear;
    border: $loader-line-width solid currentColor;
    color: $inverted-loader-line-color;
  
    box-shadow: 0 0 0 1px transparent;
  }
  @if($use-button-type-labeled) {
    .ui.labeled.icon.loading.button .icon {
      background-color: transparent;
      box-shadow: none;
    }
  }
  
  @if($use-button-type-basic) {
    .ui.basic.loading.button:not(.inverted):before {
      border-color: $loader-fill-color;
    }
    .ui.basic.loading.button:not(.inverted):after {
      border-color: $loader-line-color;
    }
  }
  /*-------------------
        Disabled
  --------------------*/
  
  .ui.buttons .disabled.button:not(.basic),
  .ui.disabled.button,
  .ui.button:disabled,
  .ui.disabled.button:hover,
  .ui.disabled.active.button {
    cursor: default;
    opacity: $disabled-opacity !important;
    background-image: none;
    box-shadow: none;
    pointer-events: none !important;
  }
  
  @if($use-button-type-basic) {
    /* Basic Group With Disabled */
    .ui.basic.buttons .ui.disabled.button {
      border-color: $disabled-border-color;
    }
  }
  /*******************************
               Types
  *******************************/
  
  @if($use-button-type-animated) {
    /*-------------------
           Animated
    --------------------*/
    .ui.animated.button {
      position: relative;
      overflow: hidden;
      padding-right: 0 !important;
      vertical-align: $button-animated-vertical-align;
      z-index: $button-animated-z-index;
    }
    
    .ui.animated.button .content {
      will-change: transform, opacity;
    }
    .ui.animated.button .visible.content {
      position: relative;
      margin-right: $button-horizontal-padding;
    }
    .ui.animated.button .hidden.content {
      position: absolute;
      width: 100%;
    }
    
    /* Horizontal */
    .ui.animated.button .visible.content,
    .ui.animated.button .hidden.content {
      transition: right $button-animation-duration $button-animation-easing 0s;
    }
    .ui.animated.button .visible.content {
      left: auto;
      right: 0;
    }
    .ui.animated.button .hidden.content {
      top: 50%;
      left: auto;
      right: -100%;
      margin-top: -($button-line-height / 2);
    }
    .ui.animated.button:focus .visible.content,
    .ui.animated.button:hover .visible.content {
      left: auto;
      right: 200%;
    }
    .ui.animated.button:focus .hidden.content,
    .ui.animated.button:hover .hidden.content {
      left: auto;
      right: 0;
    }
    
    /* Vertical */
    .ui.vertical.animated.button .visible.content,
    .ui.vertical.animated.button .hidden.content {
      transition: top $button-animation-duration $button-animation-easing, transform $button-animation-duration $button-animation-easing;
    }
    .ui.vertical.animated.button .visible.content {
      transform: translateY(0%);
      right: auto;
    }
    .ui.vertical.animated.button .hidden.content {
      top: -50%;
      left: 0;
      right: auto;
    }
    .ui.vertical.animated.button:focus .visible.content,
    .ui.vertical.animated.button:hover .visible.content {
      transform: translateY(200%);
      right: auto;
    }
    .ui.vertical.animated.button:focus .hidden.content,
    .ui.vertical.animated.button:hover .hidden.content {
      top: 50%;
      right: auto;
    }
    
    /* Fade */
    .ui.fade.animated.button .visible.content,
    .ui.fade.animated.button .hidden.content {
      transition: opacity $button-animation-duration $button-animation-easing, transform $button-animation-duration $button-animation-easing;
    }
    .ui.fade.animated.button .visible.content {
      left: auto;
      right: auto;
      opacity: 1;
      transform: scale(1);
    }
    .ui.fade.animated.button .hidden.content {
      opacity: 0;
      left: 0;
      right: auto;
      transform: scale($button-fade-scale-high);
    }
    .ui.fade.animated.button:focus .visible.content,
    .ui.fade.animated.button:hover .visible.content {
      left: auto;
      right: auto;
      opacity: 0;
      transform: scale($button-fade-scale-low);
    }
    .ui.fade.animated.button:focus .hidden.content,
    .ui.fade.animated.button:hover .hidden.content {
      left: 0;
      right: auto;
      opacity: 1;
      transform: scale(1);
    }
  }
  
  @if($use-button-type-inverted) {
    /*-------------------
           Inverted
    --------------------*/
    
    .ui.inverted.button {
      box-shadow: 0 0 0 $button-inverted-border-size $white inset;
      background: transparent none;
      color: $white;
      text-shadow: none !important;
    }
    
    /* Group */
    .ui.inverted.buttons .button {
      margin: $button-inverted-group-offset;
    }
    .ui.inverted.buttons .button:first-child {
      margin-left: 0;
    }
    .ui.inverted.vertical.buttons .button {
      margin: $button-inverted-vertical-group-offset;
    }
    .ui.inverted.vertical.buttons .button:first-child {
      margin-top: 0;
    }
    
    /* States */
    
    /* Hover */
    .ui.inverted.button:hover {
      background: $white;
      box-shadow: 0 0 0 $button-inverted-border-size $white inset;
      color: $button-hover-color;
    }
    
    /* Active / Focus */
    .ui.inverted.button:focus,
    .ui.inverted.button.active {
      background: $white;
      box-shadow: 0 0 0 $button-inverted-border-size $white inset;
      color: $button-focus-color;
    }
    
    /* Active Focus */
    .ui.inverted.button.active:focus {
      background: $mid-white;
      box-shadow: 0 0 0 $button-inverted-border-size $mid-white inset;
      color: $button-focus-color;
    }
  }
  
  
  @if($use-button-type-labeled) {
    /*-------------------
        Labeled Button
    --------------------*/
  
    .ui.labeled.button:not(.icon) {
      display: inline-flex;
      flex-direction: row;
      background: none;
      padding: 0 !important;
      border: none;
      box-shadow: none;
    }
  
    .ui.labeled.button > .button {
      margin: 0;
    }
    .ui.labeled.button > .label {
      display: flex;
      align-items: $button-labeled-label-align;
      margin: 0 0 0 $button-labeled-label-border-offset !important;
      font-size: $button-labeled-label-font-size;
      padding: $button-labeled-label-padding;
      border-color: $button-labeled-label-border-color;
    }
  
    /* Tag */
    .ui.labeled.button > .tag.label:before {
      width: $button-labeled-tag-label-size;
      height: $button-labeled-tag-label-size;
    }
  
    /* Right */
    .ui.labeled.button:not([class*="left labeled"]) > .button {
      border-top-right-radius: 0;
      border-bottom-right-radius: 0;
    }
    .ui.labeled.button:not([class*="left labeled"]) > .label {
      border-top-left-radius: 0;
      border-bottom-left-radius: 0;
    }
  
    /* Left Side */
    .ui[class*="left labeled"].button > .button {
      border-top-left-radius: 0;
      border-bottom-left-radius: 0;
    }
    .ui[class*="left labeled"].button > .label {
      border-top-right-radius: 0;
      border-bottom-right-radius: 0;
    }
  }
  
