  .ui.fixed.menu,
  .ui[class*="top fixed"].menu {
    top: 0;
    left: 0;
    right: auto;
    bottom: auto;
  }
  .ui[class*="top fixed"].menu {
    border-top: none;
    border-left: none;
    border-right: none;
  }
  .ui[class*="right fixed"].menu {
    border-top: none;
    border-bottom: none;
    border-right: none;
    top: 0;
    right: 0;
    left: auto;
    bottom: auto;
    width: auto;
    height: 100%;
  }
  .ui[class*="bottom fixed"].menu {
    border-bottom: none;
    border-left: none;
    border-right: none;
    bottom: 0;
    left: 0;
    top: auto;
    right: auto;
  }
  .ui[class*="left fixed"].menu {
    border-top: none;
    border-bottom: none;
    border-left: none;
    top: 0;
    left: 0;
    right: auto;
    bottom: auto;
    width: auto;
    height: 100%;
  }

  /* Coupling with Grid */
  .ui.fixed.menu + .ui.grid {
