  @if($use-menu-type-pointing) {
    /*-------------------
          Pointing
    --------------------*/

    .ui.pointing.menu .item:after {
      visibility: hidden;
      position: absolute;
      content: '';
      top: 100%;
      left: 50%;
      transform: translateX(-50%) translateY(-50%) rotate(45deg);
      background: none;

      margin: ($menu-arrow-border-width / 2) 0 0;
      width: $menu-arrow-size;
      height: $menu-arrow-size;

      border: none;
      border-bottom: $menu-arrow-border;
      border-right: $menu-arrow-border;

      z-index: $menu-arrow-z-index;
      transition: $menu-arrow-transition;
    }
    .ui.vertical.pointing.menu .item:after {
      position: absolute;
      top: 50%;
      right: 0;
      bottom: auto;
      left: auto;

      transform: translateX(50%) translateY(-50%) rotate(45deg);
      margin: 0 (-$menu-arrow-border-width / 2) 0 0;

