    .ui.pointing.menu .ui.dropdown .menu .item:after,
    .ui.vertical.pointing.menu .ui.dropdown .menu .item:after {
      display: none;
    }

    /* Active */
    .ui.pointing.menu .active.item:after {
      visibility: visible;
    }
    .ui.pointing.menu .active.dropdown.item:after {
      visibility: hidden;
    }

    /* Don't double up pointers */
    .ui.pointing.menu .dropdown.active.item:after,
    .ui.pointing.menu .active.item .menu .active.item:after {
      display: none;
    }

    /* Colors */
    .ui.pointing.menu .active.item:hover:after {
      background-color: $menu-arrow-hover-color;
    }
    .ui.pointing.menu .active.item:after {
      background-color: $menu-arrow-active-color;
    }
    .ui.pointing.menu .active.item:hover:after {
      background-color: $menu-arrow-active-hover-color;
    }

    .ui.vertical.pointing.menu .active.item:hover:after {
      background-color: $menu-arrow-vertical-hover-color;
    }
    .ui.vertical.pointing.menu .active.item:after {
      background-color: $menu-arrow-vertical-active-color;
    }
    .ui.vertical.pointing.menu .menu .active.item:after {
      background-color: $menu-arrow-vertical-sub-color;
    }

    @if($use-menu-variation-inverted) {
      @each $key, $value in $colors {
        @if(index(join($main-colors-names, $use-menu-variation-colors), $key+'')) {
          $color: $key;
          $c: map-get($value, color);
        
          .ui.inverted.pointing.menu .#{$key + ""}.active.item:after {
            background-color: $c;
          }
        }
      }
    }
  }



  @if($use-menu-variation-attached) {
    /*--------------
        Attached
    ---------------*/

    /* Middle */
    .ui.attached.menu {
      top: 0;
      bottom: 0;
      border-radius: 0;
      margin: 0 $menu-attached-horizontal-offset;
      width: $menu-attached-width;
      max-width: $menu-attached-width;
      box-shadow: $menu-attached-box-shadow;
    }
    .ui.attached + .ui.attached.menu:not(.top) {
      border-top: none;
    }

    /* Top */
    .ui[class*="top attached"].menu {
      bottom: 0;
      margin-bottom: 0;
      top: $menu-attached-top-offset;
      margin-top: $menu-vertical-margin;
      border-radius: $menu-border-radius $menu-border-radius 0 0;
    }
    .ui.menu[class*="top attached"]:first-child {
      margin-top: 0;
    }

    /* Bottom */
    .ui[class*="bottom attached"].menu {
      bottom: 0;
      margin-top: 0;
      top: $menu-attached-bottom-offset;
      margin-bottom: $menu-vertical-margin;
      box-shadow: $menu-attached-bottom-box-shadow;
      border-radius: 0 0 $menu-border-radius $menu-border-radius;
    }
    .ui[class*="bottom attached"].menu:last-child {
      margin-bottom: 0;
    }

    /* Attached Menu Item */
    .ui.top.attached.menu > .item:first-child {
      border-radius: $menu-border-radius 0 0 0;
    }
    .ui.bottom.attached.menu > .item:first-child {
      border-radius: 0 0 0 $menu-border-radius;
    }

    /* Tabular Attached */
    .ui.attached.menu:not(.tabular) {
      border: $menu-attached-border;
    }
    @if $use-menu-variation-inverted {
      .ui.attached.inverted.menu {
        border: none;
      }
    }
    .ui.attached.tabular.menu {
      margin-left: 0;
      margin-right: 0;
      width: 100%;
    }
  }

  /*--------------
      Sizes
  ---------------*/

  /* Mini */
  .ui.mini.menu {
    font-size: $menu-mini;
  }
  .ui.mini.vertical.menu:not(.icon) {
    width: $menu-mini-width;
  }
  .ui.mini.menu .dropdown {
    font-size: $menu-mini;
  }
  .ui.mini.menu .dropdown .menu > .item{
    font-size: $menu-mini;
  }

  /* Tiny */
  .ui.tiny.menu {
    font-size: $menu-tiny;
  }
  .ui.tiny.vertical.menu:not(.icon) {
    width: $menu-tiny-width;
  }
  .ui.tiny.menu .dropdown {
    font-size: $menu-tiny;
  }
  .ui.tiny.menu .dropdown .menu > .item{
    font-size: $menu-tiny;
  }

  /* Small */
  .ui.small.menu {
    font-size: $menu-small;
  }
  .ui.small.vertical.menu:not(.icon) {
    width: $menu-small-width;
  }
  .ui.small.menu .dropdown {
    font-size: $menu-small;
  }
  .ui.small.menu .dropdown .menu > .item{
    font-size: $menu-small;
  }

  /* Medium */
  .ui.menu {
    font-size: $medium;
  }
  .ui.vertical.menu {
    width: $menu-medium-width;
  }

  /* Large */
  .ui.large.menu {
    font-size: $menu-large;
  }
  .ui.large.vertical.menu:not(.icon) {
    width: $menu-large-width;
  }
  .ui.large.menu .dropdown {
    font-size: $menu-large;
  }
  .ui.large.menu .dropdown .menu > .item{
    font-size: $menu-large;
  }

  /* Huge */
  .ui.huge.menu {
    font-size: $menu-huge;
  }
  .ui.huge.vertical.menu:not(.icon) {
    width: $menu-huge-width;
  }
  .ui.huge.menu .dropdown {
    font-size: $menu-huge;
  }
  .ui.huge.menu .dropdown .menu > .item{
    font-size: $menu-huge;
  }

  /* Big */
  .ui.big.menu {
    font-size: $menu-big;
  }
  .ui.big.vertical.menu:not(.icon) {
    width: $menu-big-width;
  }
  .ui.big.menu .dropdown {
    font-size: $menu-big;
  }
  .ui.big.menu .dropdown .menu > .item{
    font-size: $menu-big;
  }

  /* Massive */
  .ui.massive.menu {
    font-size: $menu-massive;
  }
  .ui.massive.vertical.menu:not(.icon) {
    width: $menu-massive-width;
  }
  .ui.massive.menu .dropdown {
    font-size: $menu-massive;
  }
  .ui.massive.menu .dropdown .menu > .item{
    font-size: $menu-massive;
  }

  @if ($use-dropdown-variation-inverted) {
    /*-------------------
      Inverted dropdowns
    --------------------*/
    .ui.menu .ui.inverted.inverted.dropdown.item .menu {
      background: $menu-inverted-dropdown-background;
      box-shadow: $menu-inverted-dropdown-box-shadow;
    }

    .ui.menu .ui.inverted.dropdown .menu > .item {
      color: $menu-inverted-dropdown-item-color !important;
    }

    .ui.menu .ui.inverted.dropdown .menu > .active.item {
      background: $menu-inverted-dropdown-active-item-background !important;
      color: $menu-inverted-dropdown-active-item-color !important;
    }

    .ui.menu .ui.inverted.dropdown .menu > .item:hover {
      background: $menu-inverted-dropdown-hovered-item-background !important;
      color: $menu-inverted-dropdown-hovered-item-color !important;
    }

    .ui.menu .ui.inverted.dropdown .menu > .selected.item {
      background: $menu-inverted-dropdown-selected-item-background !important;
      color: $menu-inverted-dropdown-selected-item-color !important;
    }

    /* Vertical */
    .ui.vertical.menu .inverted.dropdown.item .menu {
      box-shadow: $menu-inverted-dropdown-box-shadow;
    }
  }
}

@import '../../themes/default/collections/menu.style';