@import '../../themes/default/elements/list.variables';
@if $use-list {
  /*******************************
  //@-        List
  *******************************/

  ul.ui.list,
  ol.ui.list,
  .ui.list {
    list-style-type: $list-style-type;
    margin: $list-margin;
    padding: $list-vertical-padding $list-horizontal-padding;
  }

  ul.ui.list:first-child,
  ol.ui.list:first-child,
  .ui.list:first-child {
    margin-top: 0;
    padding-top: 0;
  }

  ul.ui.list:last-child,
  ol.ui.list:last-child,
  .ui.list:last-child {
    margin-bottom: 0;
    padding-bottom: 0;
  }

  /*******************************
              Content
  *******************************/

  /* List Item */
  ul.ui.list li,
  ol.ui.list li,
  .ui.list > .item,
  .ui.list .list > .item {
    display: list-item;
    table-layout: fixed;
    list-style-type: $list-style-type;
    list-style-position: $list-style-position;

    padding: $list-item-padding;
    line-height: $list-item-line-height;
  }

  ul.ui.list > li:first-child:after,
  ol.ui.list > li:first-child:after,
  .ui.list > .list > .item:after,
  .ui.list > .item:after {
    content: '';
    display: block;
    height: 0;
    clear: both;
    visibility: hidden;
  }

  ul.ui.list li:first-child,
  ol.ui.list li:first-child,
  .ui.list .list > .item:first-child,
  .ui.list > .item:first-child {
    padding-top: 0;
  }
  ul.ui.list li:last-child,
  ol.ui.list li:last-child,
  .ui.list .list > .item:last-child,
  .ui.list > .item:last-child {
    padding-bottom: 0;
  }

  /* Child List */
  ul.ui.list ul,
  ol.ui.list ol,
  .ui.list .list:not(.icon) {
    clear: both;
    margin: 0;
    padding: $list-child-padding;
  }

  /* Child Item */
  ul.ui.list ul li,
  ol.ui.list ol li,
  .ui.list .list > .item {
    padding: $list-child-item-padding;
    line-height: $list-child-item-line-height;
  }


  /* Icon */
  .ui.list .list > .item > i.icon,
  .ui.list > .item > i.icon {
    display: table-cell;
    min-width: 1.55em;
    margin: 0;
    padding-top: $list-icon-offset;
    transition: $list-icon-transition;
    &:not(.loading) {
      padding-right: $list-icon-distance;
      vertical-align: $list-icon-content-vertical-align;
    }
  }
  .ui.list .list > .item > i.icon:only-child,
  .ui.list > .item > i.icon:only-child {
    display: inline-block;
    min-width: auto;
    vertical-align: $list-icon-vertical-align;
  }


  /* Image */
  .ui.list .list > .item > .image,
  .ui.list > .item > .image {
    display: table-cell;
    background-color: transparent;
    margin: 0;
    vertical-align: $list-image-align;
  }
  .ui.list .list > .item > .image:not(:only-child):not(img),
  .ui.list > .item > .image:not(:only-child):not(img) {
    padding-right: $list-image-distance;
  }
  .ui.list .list > .item > .image img,
  .ui.list > .item > .image img {
    vertical-align: $list-image-align;
  }

  .ui.list .list > .item > img.image,
  .ui.list .list > .item > .image:only-child,
  .ui.list > .item > img.image,
  .ui.list > .item > .image:only-child {
    display: inline-block;
  }

  /* Content */
  .ui.list .list > .item > .content,
  .ui.list > .item > .content {
    line-height: $list-content-line-height;
    color: $list-content-color;
  }
  .ui.list .list > .item > .image + .content,
  .ui.list .list > .item > .icon + .content,
  .ui.list > .item > .image + .content,
  .ui.list > .item > .icon + .content {
    display: table-cell;
    width: 100%;
    padding: 0 0 0 $list-content-distance;
    vertical-align: $list-content-vertical-align;
  }
  .ui.list .list > .item > .loading.icon + .content,
  .ui.list > .item > .loading.icon + .content {
    padding-left:  calc(#{$list-icon-distance} + #{$list-content-distance});
  }
  .ui.list .list > .item > img.image + .content,
  .ui.list > .item > img.image + .content {
    display: inline-block;
    width: auto;
  }
  .ui.list .list > .item > .content > .list,
  .ui.list > .item > .content > .list {
    margin-left: 0;
    padding-left: 0;
  }

  /* Header */
  .ui.list .list > .item .header,
  .ui.list > .item .header {
    display: block;
    margin: 0;
    font-family: $list-item-header-font-family;
    font-weight: $list-item-header-font-weight;
    color: $list-item-header-color;
  }

  /* Description */
  .ui.list .list > .item .description,
  .ui.list > .item .description {
    display: block;
    color: $list-item-description-color;
  }

  /* Child Link */
  .ui.list > .item a,
  .ui.list .list > .item a {
    cursor: pointer;
  }

  /* Linking Item */
  .ui.list .list > a.item,
  .ui.list > a.item {
    cursor: pointer;
    color: $list-item-link-color;
  }
  .ui.list .list > a.item:hover,
  .ui.list > a.item:hover {
    color: $list-item-link-hover-color;
  }

  /* Linked Item Icons */
  .ui.list .list > a.item > i.icon,
  .ui.list > a.item > i.icon {
    color: $list-item-link-icon-color;
  }

  /* Header Link */
  .ui.list .list > .item a.header,
  .ui.list > .item a.header {
    cursor: pointer;
    color: $list-item-header-link-color !important;
  }
  .ui.list .list > .item > a.header:hover,
  .ui.list > .item > a.header:hover {
    color: $list-item-header-link-hover-color !important;
  }

  /* Floated Content */
  .ui[class*="left floated"].list {
    float: left;
  }
  .ui[class*="right floated"].list {
    float: right;
  }

  .ui.list .list > .item [class*="left floated"],
  .ui.list > .item [class*="left floated"] {
    float: left;
    margin: $list-left-float-margin;
  }
  .ui.list .list > .item [class*="right floated"],
  .ui.list > .item [class*="right floated"] {
    float: right;
    margin: $list-right-float-margin;
  }

  /*******************************
              Coupling
  *******************************/

  .ui.menu .ui.list > .item,
  .ui.menu .ui.list .list > .item {
    display: list-item;
    table-layout: fixed;
    background-color: transparent;

    list-style-type: $list-style-type;
    list-style-position: $list-style-position;

    padding: $list-item-vertical-padding $list-item-horizontal-padding;
    line-height: $list-item-line-height;
  }
  .ui.menu .ui.list .list > .item:before,
  .ui.menu .ui.list > .item:before {
    border: none;
    background: none;
  }
  .ui.menu .ui.list .list > .item:first-child,
  .ui.menu .ui.list > .item:first-child {
    padding-top: 0;
  }
  .ui.menu .ui.list .list > .item:last-child,
  .ui.menu .ui.list > .item:last-child {
    padding-bottom: 0;
  }


  /*******************************
              Types
  *******************************/

  @if($use-list-variation-horizontal) {
    /*-------------------
          Horizontal
    --------------------*/

    .ui.horizontal.list {
      display: inline-block;
      font-size: 0;
    }
    .ui.horizontal.list > .item {
      display: inline-block;
      margin-right: $list-horizontal-spacing;
      font-size: 1rem;
    }
    .ui.horizontal.list:not(.celled) > .item:last-child {
      margin-right: 0;
      padding-right: 0;
    }
    .ui.horizontal.list .list:not(.icon) {
      padding-left: 0;
      padding-bottom: 0;
    }

    .ui.horizontal.list > .item > .image,
    .ui.horizontal.list .list > .item > .image,
    .ui.horizontal.list > .item > .icon,
    .ui.horizontal.list .list > .item > .icon,
    .ui.horizontal.list > .item > .content,
    .ui.horizontal.list .list > .item > .content {
      vertical-align: $list-horizontal-vertical-align;
    }

    /* Padding on all elements */
    .ui.horizontal.list > .item:first-child,
    .ui.horizontal.list > .item:last-child {
      padding-top: $list-item-vertical-padding;
      padding-bottom: $list-item-vertical-padding;
    }

    /* Horizontal List */
    .ui.horizontal.list > .item > i.icon {
      margin: 0;
      padding: 0 $list-horizontal-icon-distance 0 0;
    }
    .ui.horizontal.list > .item > .image + .content,
    .ui.horizontal.list > .item > .icon,
    .ui.horizontal.list > .item > .icon + .content {
      float: none;
      display: inline-block;
      width: auto;
    }
    .ui.horizontal.list > .item > .image {
      display:inline-block;
    }
  }
  /*******************************
              States
  *******************************/

  /*-------------------
        Disabled
  --------------------*/

  .ui.list .list > .disabled.item,
  .ui.list > .disabled.item {
    pointer-events: none;
    color: $list-disabled-color !important;
  }
  .ui.inverted.list .list > .disabled.item,
  .ui.inverted.list > .disabled.item {
    color: $list-inverted-disabled-color !important;
  }

  /*-------------------
          Hover
  --------------------*/

  .ui.list .list > a.item:hover > .icon,
  .ui.list > a.item:hover > .icon {
    color: $list-item-link-icon-hover-color;
  }


  /*******************************
            Variations
  *******************************/

  @if($use-list-variation-inverted) {
    /*-------------------
          Inverted
    --------------------*/

    .ui.inverted.list .list > a.item > .icon,
    .ui.inverted.list > a.item > .icon {
      color: $list-inverted-icon-link-color;
    }
    .ui.inverted.list .list > .item .header,
    .ui.inverted.list > .item .header {
      color: $list-inverted-header-color;
    }
    .ui.inverted.list .list > .item .description,
    .ui.inverted.list > .item .description {
      color: $list-inverted-description-color;
    }
    .ui.inverted.list .list > .item > .content,
    .ui.inverted.list > .item > .content {
      color: $list-inverted-content-color;
    }
    /* Item Link */
    .ui.inverted.list .list > a.item,
    .ui.inverted.list > a.item {
      cursor: pointer;
      color: $list-inverted-item-link-color;
    }
    .ui.inverted.list .list > a.item:hover,
    .ui.inverted.list > a.item:hover {
      color: $list-inverted-item-link-hover-color;
    }


    /* Linking Content */
    .ui.inverted.list .item a:not(.ui) {
      color: $list-inverted-item-link-color !important;
    }
    .ui.inverted.list .item a:not(.ui):hover {
      color: $list-inverted-item-link-hover-color !important;
    }
  }

  /*-------------------
        Aligned
  --------------------*/

  .ui.list[class*="top aligned"] .image,
  .ui.list[class*="top aligned"] .content,
  .ui.list [class*="top aligned"] {
    vertical-align: top !important;
  }
  .ui.list[class*="middle aligned"] .image,
  .ui.list[class*="middle aligned"] .content,
  .ui.list [class*="middle aligned"] {
    vertical-align: middle !important;
  }
  .ui.list[class*="bottom aligned"] .image,
  .ui.list[class*="bottom aligned"] .content,
  .ui.list [class*="bottom aligned"] {
    vertical-align: bottom !important;
  }

  /*-------------------
        Link
  --------------------*/

  .ui.link.list .item,
  .ui.link.list a.item,
  .ui.link.list .item a:not(.ui) {
    color: $list-link-item-color;
    transition: $list-link-transition;
  }
  .ui.link.list.list a.item:hover,
  .ui.link.list.list .item a:not(.ui):hover {
    color: $list-link-item-hover-color;
  }
  .ui.link.list.list a.item:active,
  .ui.link.list.list .item a:not(.ui):active {
    color: $list-link-item-down-color;
  }
  .ui.link.list.list .active.item,
  .ui.link.list.list .active.item a:not(.ui) {
    color: $list-link-item-active-color;
  }

  @if($use-list-variation-inverted) {
    /* Inverted */
    .ui.inverted.link.list .item,
    .ui.inverted.link.list a.item,
    .ui.inverted.link.list .item a:not(.ui) {
      color: $list-inverted-link-item-color;
    }
    .ui.inverted.link.list.list a.item:hover,
    .ui.inverted.link.list.list .item a:not(.ui):hover {
      color: $list-inverted-link-item-hover-color;
    }
    .ui.inverted.link.list.list a.item:active,
    .ui.inverted.link.list.list .item a:not(.ui):active {
      color: $list-inverted-link-item-down-color;
    }
    .ui.inverted.link.list.list a.active.item,
    .ui.inverted.link.list.list .active.item a:not(.ui) {
      color: $list-inverted-link-item-active-color;
    }
  }


  @if($use-list-variation-selection) {
    /*-------------------
          Selection
    --------------------*/

    .ui.selection.list .list > .item,
    .ui.selection.list > .item {
      cursor: pointer;
      background: $list-selection-background;
      padding: $list-selection-item-vertical-padding $list-selection-item-horizontal-padding;
      margin: $list-selection-item-margin;
      color: $list-selection-color;
      border-radius: $list-selection-item-border-radius;
      transition: $list-selection-transition;
    }
    .ui.selection.list .list > .item:last-child,
    .ui.selection.list > .item:last-child {
      margin-bottom: 0;
    }
    .ui.selection.list .list > .item:hover,
    .ui.selection.list > .item:hover {
      background: $list-selection-hover-background;
      color: $list-selection-hover-color;
    }
    .ui.selection.list .list > .item:active,
    .ui.selection.list > .item:active {
      background: $list-selection-down-background;
      color: $list-selection-down-color;
    }
    .ui.selection.list .list > .item.active,
    .ui.selection.list > .item.active {
      background: $list-selection-active-background;
      color: $list-selection-active-color;
    }

    @if($use-list-variation-inverted) {
      /* Inverted */
      .ui.inverted.selection.list > .item {
        background: $list-inverted-selection-background;
        color: $list-inverted-selection-color;
      }
      .ui.inverted.selection.list > .item:hover {
        background: $list-inverted-selection-hover-background;
        color: $list-inverted-selection-hover-color;
      }
      .ui.inverted.selection.list > .item:active {
        background: $list-inverted-selection-down-background;
        color: $list-inverted-selection-down-color;
      }
      .ui.inverted.selection.list > .item.active {
        background: $list-inverted-selection-active-background;
        color: $list-inverted-selection-active-color;
      }
    }
  }

  /* Celled / Divided Selection List */
  .ui.celled.selection.list .list > .item,
  .ui.divided.selection.list .list > .item,
  .ui.celled.selection.list > .item,
  .ui.divided.selection.list > .item {
    border-radius: 0;
  }

  /*-------------------
        Animated
  --------------------*/

  .ui.animated.list > .item {
    transition: $list-animated-transition;
  }
  .ui.animated.list:not(.horizontal) > .item:hover {
    padding-left: $list-animated-indent;
  }

  /*-------------------
        Fitted
  --------------------*/
  .ui.fitted.list:not(.selection) .list > .item,
  .ui.fitted.list:not(.selection) > .item {
    padding-left: 0;
    padding-right: 0;
  }
  .ui.fitted.selection.list .list > .item,
  .ui.fitted.selection.list > .item {
    margin-left: -$list-selection-item-horizontal-padding;
    margin-right: -$list-selection-item-horizontal-padding;
  }


  @if($use-list-type-bulleted) {
    /*-------------------
          Bulleted
    --------------------*/

    ul.ui.list,
    .ui.bulleted.list {
      margin-left: $list-bullet-distance;
    }
    ul.ui.list li,
    .ui.bulleted.list .list > .item,
    .ui.bulleted.list > .item {
      position: relative;
    }
    ul.ui.list li:before,
    .ui.bulleted.list .list > .item:before,
    .ui.bulleted.list > .item:before {
      user-select: none;
      pointer-events: none;
      position: absolute;
      top: auto;
      left: auto;
      font-weight: $normal;
      margin-left: $list-bullet-offset;
      content: $list-bullet-character;
      opacity: $list-bullet-opacity;
      color: $list-bullet-color;
      vertical-align: $list-bullet-vertical-align;
    }

    ul.ui.list li:before,
    .ui.bulleted.list .list > a.item:before,
    .ui.bulleted.list > a.item:before {
      color: $list-bullet-link-color;
    }

    ul.ui.list ul,
    .ui.bulleted.list .list:not(.icon) {
      padding-left: $list-bullet-child-distance;
    }

    /* Horizontal Bulleted */
    ul.ui.horizontal.bulleted.list,
    .ui.horizontal.bulleted.list {
      margin-left: 0;
    }
    ul.ui.horizontal.bulleted.list li,
    .ui.horizontal.bulleted.list > .item {
      margin-left: $list-horizontal-bullet-spacing;
    }
    ul.ui.horizontal.bulleted.list li:first-child,
    .ui.horizontal.bulleted.list > .item:first-child {
      margin-left: 0;
    }
    ul.ui.horizontal.bulleted.list li::before,
    .ui.horizontal.bulleted.list > .item::before {
      color: $list-horizontal-bullet-color;
    }
    ul.ui.horizontal.bulleted.list li:first-child::before,
    .ui.horizontal.bulleted.list > .item:first-child::before {
      display: none;
    }
  }

  @if($use-list-type-ordered) {
    /*-------------------
          Ordered
    --------------------*/

    ol.ui.list,
    .ui.ordered.list,
    .ui.ordered.list .list:not(.icon),
    ol.ui.list ol {
      counter-reset: ordered;
      margin-left: $list-ordered-count-distance;
      list-style-type: none;
    }
    ol.ui.list li,
    .ui.ordered.list .list > .item,
    .ui.ordered.list > .item {
      list-style-type: none;
      position: relative;
    }
    ol.ui.list li:before,
    .ui.ordered.list .list > .item:before,
    .ui.ordered.list > .item:before {
      position: absolute;
      top: auto;
      left: auto;
      user-select: none;
      pointer-events: none;
      margin-left: -($list-ordered-count-distance);
      counter-increment: $list-ordered-count-name;
      content: $list-ordered-count-content;
      text-align: $list-ordered-count-text-align;
      color: $list-ordered-count-color;
      vertical-align: $list-ordered-count-vertical-align;
      opacity: $list-ordered-count-opacity;
    }

    ol.ui.inverted.list li:before,
    .ui.ordered.inverted.list .list > .item:before,
    .ui.ordered.inverted.list > .item:before {
      color: $list-ordered-inverted-count-color;
    }

    /* Value */
    .ui.ordered.list .list > .item[data-value]:before,
    .ui.ordered.list > .item[data-value]:before {
      content: attr(data-value);
    }
    ol.ui.list li[value]:before {
      content: attr(value);
    }

    /* Child Lists */
    ol.ui.list ol,
    .ui.ordered.list .list:not(.icon) {
      margin-left: $list-ordered-child-count-distance;
    }
    ol.ui.list ol li:before,
    .ui.ordered.list .list > .item:before {
      margin-left: $list-ordered-child-count-offset;
    }

    @if($use-list-variation-horizontal) {
      /* Horizontal Ordered */
      ol.ui.horizontal.list,
      .ui.ordered.horizontal.list {
        margin-left: 0;
      }
      ol.ui.horizontal.list li:before,
      .ui.ordered.horizontal.list .list > .item:before,
      .ui.ordered.horizontal.list > .item:before {
        position: static;
        margin: 0 $list-horizontal-ordered-count-distance 0 0;
      }
    }

    /* Suffixed Ordered */
    ol.ui.suffixed.list li:before,
    .ui.suffixed.ordered.list .list > .item:before,
    .ui.suffixed.ordered.list > .item:before {
      content: $list-ordered-count-content-suffixed;
    }
  }


  @if($use-list-variation-divided) {
    /*-------------------
          Divided
    --------------------*/

    .ui.divided.list > .item {
      border-top: $list-divided-border;
    }
    .ui.divided.list .list > .item {
      border-top: $list-divided-child-border;
    }
    .ui.divided.list .item .list > .item {
      border-top: $list-divided-child-item-border;
    }
    .ui.divided.list .list > .item:first-child,
    .ui.divided.list > .item:first-child {
      border-top: none;
    }

    /* Sub Menu */
    .ui.divided.list:not(.horizontal) .list > .item:first-child {
      border-top-width: $list-divided-border-width;
    }

    /* Divided bulleted */
    .ui.divided.bulleted.list:not(.horizontal),
    .ui.divided.bulleted.list .list:not(.icon) {
      margin-left: 0;
      padding-left: 0;
    }
    .ui.divided.bulleted.list > .item:not(.horizontal) {
      padding-left: $list-bullet-distance;
    }

    /* Divided Ordered */
    .ui.divided.ordered.list {
      margin-left: 0;
    }
    .ui.divided.ordered.list .list > .item,
    .ui.divided.ordered.list > .item {
      padding-left: $list-ordered-count-distance;
    }
    .ui.divided.ordered.list .item .list:not(.icon) {
      margin-left: 0;
      margin-right: 0;
      padding-bottom: $list-item-vertical-padding;
    }
    .ui.divided.ordered.list .item .list > .item {
      padding-left: $list-ordered-child-count-distance;
    }

    /* Divided Selection */
    .ui.divided.selection.list .list > .item,
    .ui.divided.selection.list > .item {
      margin: 0;
      border-radius: 0;
    }

    @if($use-list-variation-horizontal) {
      /* Divided horizontal */
      .ui.divided.horizontal.list {
        margin-left: 0;
      }
      .ui.divided.horizontal.list > .item {
        padding-left: $list-horizontal-divided-spacing;
      }
      .ui.divided.horizontal.list > .item:not(:last-child) {
        padding-right: $list-horizontal-divided-spacing;
      }
      .ui.divided.horizontal.list > .item {
        border-top: none;
        border-right: $list-divided-border;
        margin: 0;
        line-height: $list-horizontal-divided-line-height;
      }
      .ui.horizontal.divided.list > .item:last-child {
        border-right: none;
      }
    }

    @if($use-list-variation-inverted) {
      /* Inverted */
      .ui.divided.inverted.list > .item,
      .ui.divided.inverted.list > .list,
      .ui.divided.inverted.horizontal.list > .item {
        border-color: $list-divided-inverted-border-color;
      }
    }
  }


  @if($use-list-variation-celled) {
    /*-------------------
            Celled
    --------------------*/

    .ui.celled.list > .item,
    .ui.celled.list > .list {
      border-top: $list-celled-border;
      padding-left: $list-celled-horizontal-padding;
      padding-right: $list-celled-horizontal-padding;
    }
    .ui.celled.list > .item:last-child {
      border-bottom: $list-celled-border;
    }

    /* Padding on all elements */
    .ui.celled.list > .item:first-child,
    .ui.celled.list > .item:last-child {
      padding-top: $list-item-vertical-padding;
      padding-bottom: $list-item-vertical-padding;
    }

    /* Sub Menu */
    .ui.celled.list .item .list > .item {
      border-width: 0;
    }
    .ui.celled.list .list > .item:first-child {
      border-top-width: 0;
    }

    @if($use-list-type-bulleted) {
      /* Celled Bulleted */
      .ui.celled.bulleted.list {
        margin-left: 0;
      }
      .ui.celled.bulleted.list .list > .item,
      .ui.celled.bulleted.list > .item {
        padding-left: ($list-bullet-distance);
      }
      .ui.celled.bulleted.list .item .list:not(.icon) {
        margin-left: -($list-bullet-distance);
        margin-right: -($list-bullet-distance);
        padding-bottom: $list-item-vertical-padding;
      }
    }

    @if($use-list-type-ordered) {
      /* Celled Ordered */
      .ui.celled.ordered.list {
        margin-left: 0;
      }
      .ui.celled.ordered.list .list > .item,
      .ui.celled.ordered.list > .item {
        padding-left: $list-ordered-count-distance;
      }
      .ui.celled.ordered.list .item .list:not(.icon) {
        margin-left: 0;
        margin-right: 0;
        padding-bottom: $list-item-vertical-padding;
      }
      .ui.celled.ordered.list .list > .item {
        padding-left: $list-ordered-child-count-distance;
      }
    }

    @if($use-list-variation-horizontal) {
      /* Celled Horizontal */
      .ui.horizontal.celled.list {
        margin-left: 0;
      }
      .ui.horizontal.celled.list .list > .item,
      .ui.horizontal.celled.list > .item {
        border-top: none;
        border-left: $list-celled-border;
        margin: 0;
        padding-left: $list-horizontal-celled-spacing;
        padding-right: $list-horizontal-celled-spacing;

        line-height: $list-horizontal-celled-line-height;
      }
      .ui.horizontal.celled.list .list > .item:last-child,
      .ui.horizontal.celled.list > .item:last-child {
        border-bottom: none;
        border-right: $list-celled-border;
      }
    }

    @if($use-list-variation-inverted) {
      /* Inverted */
      .ui.celled.inverted.list > .item,
      .ui.celled.inverted.list > .list {
        border-color: $list-celled-inverted-border;
      }
      .ui.celled.inverted.horizontal.list .list > .item,
      .ui.celled.inverted.horizontal.list > .item {
        border-color: $list-celled-inverted-border;
      }
    }
  }


  @if($use-list-variation-relaxed) {
    /*-------------------
          Relaxed
    --------------------*/

    .ui.relaxed.list:not(.horizontal) > .item:not(:first-child) {
      padding-top: $list-relaxed-item-vertical-padding;
    }
    .ui.relaxed.list:not(.horizontal) > .item:not(:last-child) {
      padding-bottom: $list-relaxed-item-vertical-padding;
    }
    .ui.horizontal.relaxed.list .list > .item:not(:first-child),
    .ui.horizontal.relaxed.list > .item:not(:first-child) {
      padding-left: $list-relaxed-horizontal-padding;
    }
    .ui.horizontal.relaxed.list .list > .item:not(:last-child),
    .ui.horizontal.relaxed.list > .item:not(:last-child) {
      padding-right: $list-relaxed-horizontal-padding;
    }

    /* Very Relaxed */
    .ui[class*="very relaxed"].list:not(.horizontal) > .item:not(:first-child) {
      padding-top: $list-very-relaxed-item-vertical-padding;
    }
    .ui[class*="very relaxed"].list:not(.horizontal) > .item:not(:last-child) {
      padding-bottom: $list-very-relaxed-item-vertical-padding;
    }
    .ui.horizontal[class*="very relaxed"].list .list > .item:not(:first-child),
    .ui.horizontal[class*="very relaxed"].list > .item:not(:first-child) {
      padding-left: $list-very-relaxed-horizontal-padding;
    }
    .ui.horizontal[class*="very relaxed"].list .list > .item:not(:last-child),
    .ui.horizontal[class*="very relaxed"].list > .item:not(:last-child) {
      padding-right: $list-very-relaxed-horizontal-padding;
    }
  }
  /*-------------------
        Sizes
  --------------------*/

  .ui.mini.list {
    font-size: $relative-mini;
  }
  .ui.tiny.list {
    font-size: $relative-tiny;
  }
  .ui.small.list {
    font-size: $relative-small;
  }
  .ui.list {
    font-size: $relative-medium;
  }
  .ui.large.list {
    font-size: $relative-large;
  }
  .ui.big.list {
    font-size: $relative-big;
  }
  .ui.huge.list {
    font-size: $relative-huge;
  }
  .ui.massive.list {
    font-size: $relative-massive;
  }

  .ui.mini.horizontal.list .list > .item,
  .ui.mini.horizontal.list > .item {
    font-size: $mini;
  }
  .ui.tiny.horizontal.list .list > .item,
  .ui.tiny.horizontal.list > .item {
    font-size: $tiny;
  }
  .ui.small.horizontal.list .list > .item,
  .ui.small.horizontal.list > .item {
    font-size: $small;
  }
  .ui.horizontal.list .list > .item,
  .ui.horizontal.list > .item {
    font-size: $medium;
  }
  .ui.large.horizontal.list .list > .item,
  .ui.large.horizontal.list > .item {
    font-size: $large;
  }
  .ui.big.horizontal.list .list > .item,
  .ui.big.horizontal.list > .item {
    font-size: $big;
  }
  .ui.huge.horizontal.list .list > .item,
  .ui.huge.horizontal.list > .item {
    font-size: $huge;
  }
  .ui.massive.horizontal.list .list > .item,
  .ui.massive.horizontal.list > .item {
    font-size: $massive;
  }
}

@import '../../themes/default/elements/list.style';