@import '../../themes/default/collections/grid.variables';

@if $use-grid {
  /*******************************
  //@-        Standard
  *******************************/

  .ui.grid {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: stretch;
    padding: 0;
  }

  /*----------------------
        Remove Gutters
  -----------------------*/

  .ui.grid {
    margin-top: -($grid-row-spacing / 2);
    margin-bottom: -($grid-row-spacing / 2);
    margin-left: -($grid-gutter-width / 2);
    margin-right: -($grid-gutter-width / 2);
  }
  .ui.relaxed.grid  {
    margin-left: -($grid-relaxed-gutter-width / 2);
    margin-right: -($grid-relaxed-gutter-width / 2);
  }
  .ui[class*="very relaxed"].grid  {
    margin-left: -($grid-very-relaxed-gutter-width / 2);
    margin-right: -($grid-very-relaxed-gutter-width / 2);
  }


  /* Preserve Rows Spacing on Consecutive Grids */
  .ui.grid + .grid {
    margin-top: $grid-consecutive-distance;
  }

  /*-------------------
        Columns
  --------------------*/

  /* Standard 16 column */
  .ui.grid > .column:not(.row),
  .ui.grid > .row > .column {
    position: relative;
    display: inline-block;

    width: $one-wide;
    padding-left: ($grid-gutter-width / 2);
    padding-right: ($grid-gutter-width / 2);
    vertical-align: top;
  }

  .ui.grid > * {
    padding-left: ($grid-gutter-width / 2);
    padding-right: ($grid-gutter-width / 2);
  }

  /*-------------------
          Rows
  --------------------*/

  .ui.grid > .row {
    position: relative;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: inherit;
    align-items: stretch;
    width: 100% !important;
    padding: 0;
    padding-top: ($grid-row-spacing / 2);
    padding-bottom: ($grid-row-spacing / 2);
  }

  /*-------------------
        Columns
  --------------------*/

  /* Vertical padding when no rows */
  .ui.grid > .column:not(.row) {
    padding-top: ($grid-row-spacing / 2);
    padding-bottom: ($grid-row-spacing / 2);
  }
  .ui.grid > .row > .column {
    margin-top: 0;
    margin-bottom: 0;
  }

  /*-------------------
        Content
  --------------------*/

  .ui.grid > .row > img,
  .ui.grid > .row > .column > img {
    max-width: $grid-column-max-image-width;
  }

  /*-------------------
      Loose Coupling
  --------------------*/

  /* Collapse Margin on Consecutive Grid */
  .ui.grid > .ui.grid:first-child {
    margin-top: 0;
  }
  .ui.grid > .ui.grid:last-child {
    margin-bottom: 0;
  }

  /* Segment inside Aligned Grid */
  .ui.grid .aligned.row > .column > .segment:not(.compact):not(.attached),
  .ui.aligned.grid .column > .segment:not(.compact):not(.attached) {
    width: 100%;
  }

  /* Align Dividers with Gutter */
  .ui.grid .row + .ui.divider {
    flex-grow: 1;
    margin: ($grid-row-spacing / 2) ($grid-gutter-width / 2);
  }
  .ui.grid .column + .ui.vertical.divider {
    height: calc(50% - #{$grid-row-spacing / 2});
  }

  /* Remove Border on Last Horizontal Segment */
  .ui.grid > .row > .column:last-child > .horizontal.segment,
  .ui.grid > .column:last-child > .horizontal.segment {
    box-shadow: none;
  }

  /*******************************
            Variations
  *******************************/


  /*-----------------------
        Page Grid
  -------------------------*/

  @media only screen and (max-width: $largest-mobile-screen) {
    .ui.page.grid {
      width: $grid-mobile-width;
      padding-left: $grid-mobile-gutter;
      padding-right: $grid-mobile-gutter;
