  /*----------------------
          Relaxed
  -----------------------*/

  .ui.relaxed.grid > .column:not(.row),
  .ui.relaxed.grid > .row > .column,
  .ui.grid > .relaxed.row > .column {
    padding-left: ($grid-relaxed-gutter-width / 2);
