  /* Coupling with UI Divider */
  .ui.relaxed.grid .row + .ui.divider,
  .ui.grid .relaxed.row + .ui.divider {
    margin-left: ($grid-relaxed-gutter-width / 2);
    margin-right: ($grid-relaxed-gutter-width / 2);
