    margin-right: ($grid-relaxed-gutter-width / 2);
    width: calc(100% - #{$grid-relaxed-gutter-width});
  }
  .ui[class*="very relaxed"][class*="vertically divided"].grid > .row:before {
    margin-left: ($grid-very-relaxed-gutter-width / 2);
    margin-right: ($grid-very-relaxed-gutter-width / 2);
    width: calc(100% - #{$grid-very-relaxed-gutter-width});
  }

  /*----------------------
          Celled
  -----------------------*/

  .ui.celled.grid {
    width: 100%;
    margin: $grid-celled-margin;
    box-shadow: $grid-celled-divider;
  }

  .ui.celled.grid > .row {
    width: 100% !important;
    margin: 0;
    padding: 0;
    box-shadow: $grid-celled-row-divider;
