  /*----------------------
    Vertically Aligned
  -----------------------*/

  /* Top Aligned */
  .ui[class*="top aligned"].grid > .column:not(.row),
  .ui[class*="top aligned"].grid > .row > .column,
  .ui.grid > [class*="top aligned"].row > .column,
  .ui.grid > [class*="top aligned"].column:not(.row),
